.eir2-component {
  display: block;
  width: auto;
  height: 100%;
}
.eir2-component .is-hidden {
  display: none;
}
component-container.is-hidden {
  display: none;
}
children-component-container {
  display: flex;
  width: 100%;
  height: 100%;
}
component-container {
  display: block;
  height: 100%;
}
component-container .child-cage {
  display: flex;
  /* Used for alignment setting */
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}
component-container .child-cage children-component-container.vertical {
  flex-direction: column;
}
component-container .child-cage children-component-container.vertical component-container.eir2-component {
  height: auto;
}
.eir2-list-component .full-height {
  height: calc(100% - 10px);
  margin: 0;
  background-color: transparent;
  border: 1px solid #bdbdbd;
}
.eir2-list-component .full-height soho-toolbar-flex {
  padding: 13px 20px;
  border-bottom: 1px solid #bdbdbd;
}
.eir2-list-component .full-height soho-listview {
  height: calc(100% - 47px);
  overflow-y: scroll;
}
.eir2-list-component .full-height soho-listview li {
  font-size: 1.2rem;
  background-color: transparent;
}
.eir2-list-component .full-height soho-listview li.is-selected {
  background-color: #c8e9f4;
}
