}
eir-interaction-settings .buttonContainer #multicall-tooltip {
  align-self: center;
  margin-right: auto;
}
eir-interaction-settings .buttonContainer .btn-icon {
  margin-right: 0;
}
eir-interaction-settings eir-parameter-handler .parameter-btn,
eir-interaction-settings eir-parameter-handler .parameter-btn:hover,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled],
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover,
eir-interaction-settings eir-parameter-handler .parameter-btn:focus,
eir-interaction-settings eir-parameter-handler .parameter-btn:active {
  width: 100%;
  background-color: #313236;
  border: none;
}
eir-interaction-settings eir-parameter-handler .parameter-btn:hover:not([disabled]) svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:hover:hover:not([disabled]) svg,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover:not([disabled]) svg,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover:hover:not([disabled]) svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:focus:hover:not([disabled]) svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:active:hover:not([disabled]) svg {
  color: #fff;
}
eir-interaction-settings eir-parameter-handler .parameter-btn:hover:not([disabled]) span,
eir-interaction-settings eir-parameter-handler .parameter-btn:hover:hover:not([disabled]) span,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover:not([disabled]) span,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover:hover:not([disabled]) span,
eir-interaction-settings eir-parameter-handler .parameter-btn:focus:hover:not([disabled]) span,
eir-interaction-settings eir-parameter-handler .parameter-btn:active:hover:not([disabled]) span {
  color: #fff;
}
eir-interaction-settings eir-parameter-handler .parameter-btn svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:hover svg,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled] svg,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:focus svg,
eir-interaction-settings eir-parameter-handler .parameter-btn:active svg {
  color: #abaeb7;
}
eir-interaction-settings eir-parameter-handler .parameter-btn span,
eir-interaction-settings eir-parameter-handler .parameter-btn:hover span,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled] span,
eir-interaction-settings eir-parameter-handler .parameter-btn[disabled]:hover span,
eir-interaction-settings eir-parameter-handler .parameter-btn:focus span,
eir-interaction-settings eir-parameter-handler .parameter-btn:active span {
  padding-left: 7px;
  color: #abaeb7;
  font-size: 12px;
}
eir-interaction-settings eir-parameter-handler hr {
  margin: 20px 0;
  border-color: #212224;
  border-top: none;
}
eir-interaction-settings .edit-topic {
  position: inherit;
}
eir-interaction-settings .edit-topic > .settingsTitle {
  margin-top: 30px;
}
eir-interaction-settings .interaction-container {
  padding: 0 10px 10px;
}
eir-interaction-settings .interaction-list {
  outline: none;
}
eir-interaction-settings .interaction-list .interaction-list-item {
  display: flex;
  align-items: center;
  height: 30px;
  color: #c8cbd4;
  background-color: #414247;
  border-bottom: none;
  border-radius: 2px;
}
eir-interaction-settings .interaction-list .interaction-list-item:focus {
  outline: none;
}
eir-interaction-settings .interaction-list .interaction-list-item:hover {
  background-color: #505259;
}
eir-interaction-settings .interaction-list .interaction-list-item.is-selected {
  color: #fff;
  background-color: #656871;
  border-left: 3px solid #368ac0;
}
eir-interaction-settings .interaction-list .interaction-list-item.is-selected > span:first-of-type {
  padding-left: 7px;
}
eir-interaction-settings .interaction-list .interaction-list-item:not(:last-of-type) {
  margin-bottom: 2px;
}
eir-interaction-settings .interaction-list .interaction-list-item > div {
  width: 15px;
  height: inherit;
  border-right: 1px solid #313236;
  border-left: 1px solid #313236;
}
eir-interaction-settings .interaction-list .interaction-list-item > div svg {
  width: 15px;
  height: 100%;
  color: #c8cbd4;
}
eir-interaction-settings .interaction-list .interaction-list-item > span {
  width: 100%;
  padding: 0 10px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
eir-interaction-settings .settings-content {
  padding: 20px 10px;
}
eir-interaction-settings .data-mapping-content {
  padding: 10px;
  background-color: #212224;
}
eir-interaction-settings .data-mapping-content .parameter-item {
  margin-bottom: 4px;
  padding: 20px 10px;
  background-color: #313236;
}
eir-interaction-settings .data-mapping-content .parameter-item .metadata-heading {
  display: none;
}
eir-interaction-settings .settingstopic .settings-title {
  height: 36px;
  padding: 11px 10px;
  background-color: #414247;
  border-top: 1px solid #212224;
  border-bottom: 1px solid #212224;
}
eir-interaction-settings .settingstopic .settings-title label {
  margin: 0;
  font-size: 13px;
}
